import { Box, Grid, Hidden } from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"
import { graphql, PageProps } from "gatsby"
import React from "react"
import { BlogIndexQuery } from "../../graphql-types"
import CategoryList from "./indexPageComponents/CategoryList"
import CategorySelect from "./indexPageComponents/CategorySelect"
import Layout from "../components/Layout"
import PostListItem from "./indexPageComponents/PostListItem"
import SEO from "../components/seo"
import SiteTitle from "./indexPageComponents/SiteTitle"
import PostCard from "./indexPageComponents/PostCard"

const BlogIndex: React.FC<PageProps<BlogIndexQuery>> = ({
  data,
  location,
  pageContext,
}) => {
  const classes = useStyles()
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  return (
    <Layout siteTitle={siteTitle}>
      <SEO title="All posts" />
      <Grid container>
        <Grid item xs={12}>
          <Box className={classes.titleBox}>
            <SiteTitle siteTitle={siteTitle} />
          </Box>
        </Grid>
        <Hidden mdUp>
          <Grid item xs={12}>
            <CategorySelect selectedCategory="All" />
          </Grid>
        </Hidden>
        <Grid item xs={12} md={9} lg={8}>
          <Grid container className={classes.postListContainer}>
            {posts.map(post => {
              return (
                <Grid item xs={12} key={post.fields.slug}>
                  <Hidden xsDown>
                    <PostListItem post={post} />
                  </Hidden>
                  <Hidden smUp>
                    <PostCard post={post} />
                  </Hidden>
                </Grid>
              )
            })}
          </Grid>
        </Grid>
        <Hidden smDown>
          <Grid item xs={12} md={3} lg={4} className={classes.categoryList}>
            <CategoryList selectedCategory="All" />
          </Grid>
        </Hidden>
      </Grid>
    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query BlogIndex {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt(pruneLength: 500)
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          thumbnail {
            childImageSharp {
              fluid(maxWidth: 600, quality: 100) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  }
`
const useStyles = makeStyles(theme => ({
  categoryList: {
    position: "sticky",
    marginTop: "20px",
    top: "0px",
    alignSelf: "flex-start",
  },
  postListContainer: {
    margin: theme.spacing(0, 0, 7, 0),
  },
  titleBox: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(0, 0, 7, 0),
  },
}))
