import {
  Chip,
  List,
  ListItem,
  ListItemText,
  makeStyles,
} from "@material-ui/core"
import { graphql, useStaticQuery } from "gatsby"
import kebabCase from "lodash/kebabCase"
import React from "react"
import { CategoryListQuery } from "../../../graphql-types"

const CategoryList = ({ selectedCategory }) => {
  const classes = useStyles()

  const data = useStaticQuery(graphql`
    query CategoryList {
      totalCount: allMarkdownRemark(limit: 2000) {
        totalCount
      }
      groupedCategory: allMarkdownRemark(limit: 2000) {
        group(field: frontmatter___categories) {
          fieldValue
          totalCount
        }
      }
    }
  `) as CategoryListQuery

  return (
    <React.Fragment>
      <List component="ul">
        <ListItem
          key="All"
          selected={"All" === selectedCategory}
          button
          component="a"
          href={`/`}
        >
          <Chip label={data.totalCount.totalCount} className={classes.count} />
          <ListItemText primary="All" />
        </ListItem>
        {data.groupedCategory.group.map(category => {
          return (
            <ListItem
              key={category.fieldValue}
              button
              component="a"
              selected={category.fieldValue === selectedCategory}
              href={`/categories/${kebabCase(category.fieldValue)}`}
            >
              <Chip label={category.totalCount} className={classes.count} />
              <ListItemText primary={category.fieldValue} />
            </ListItem>
          )
        })}
      </List>
    </React.Fragment>
  )
}

export default CategoryList

const useStyles = makeStyles(theme => ({
  count: {
    width: "42px",
    borderRadius: "4px",
    margin: theme.spacing(0, 2, 0, 0),
  },
}))
