import { Card, CardActionArea, Grid, Typography } from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"
import { navigate } from "gatsby"
import Img from "gatsby-image"
import React from "react"

const PostListItem = ({ post }) => {
  const classes = useStyles()
  const title = post.frontmatter.title || post.fields.slug

  return (
    <Card
      variant="outlined"
      onClick={() => setTimeout(() => navigate(post.fields.slug), 300)}
      className={classes.postItemCard}
    >
      <CardActionArea className={classes.postItemCardArea}>
        <Grid container className={classes.postItemLayout}>
          <Grid item xs={3} className={classes.thumbnailBox}>
            {(post.frontmatter as any).thumbnail !== null && (
              <Img
                fluid={
                  (post.frontmatter as any).thumbnail.childImageSharp.fluid
                }
                className={classes.thumbnail}
              />
            )}
          </Grid>
          <Grid item xs={9} className={classes.allTextBox}>
            <Typography variant="h3" className={classes.titleText}>
              {title}
            </Typography>
            <small>{post.frontmatter.date}</small>
            <span
              className={classes.decriptionText}
              dangerouslySetInnerHTML={{
                __html: post.frontmatter.description || post.excerpt,
              }}
              itemProp="description"
            />
          </Grid>
        </Grid>
      </CardActionArea>
    </Card>
  )
}

export default PostListItem

const useStyles = makeStyles(theme => ({
  postItemCardArea: {},
  postItemCard: {
    margin: theme.spacing(2, 0, 0, 0),
    borderRadius: "15px",
    boxShadow: "0px 10px 48px -16px rgba(0, 0, 0, 0.25)",
  },
  titleBox: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(0, 0, 7, 0),
  },
  thumbnailBox: {
    height: "100%",
  },
  allTextBox: {
    flex: "1",
    display: "flex",
    flexDirection: "column",
    alignContent: "center",
    padding: theme.spacing(3),
    overflow: "hidden",
    justifyContent: "center",
  },
  thumbnail: {
    height: "100%",
  },
  postItemLayout: {
    height: "180px",
    display: "flex",
    flexDirection: "row",
  },
  titleText: {},
  dateText: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
  decriptionText: {
    ...theme.typography.body1,
    padding: theme.spacing(1, 0, 0, 0),
    fontSize: "16px",
    lineHeight: "1.5",
    display: "-webkit-box",
    overflow: "hidden",
    textOverflow: "ellipsis",
    "-webkit-line-clamp": 3,
    "-webkit-box-orient": "vertical",
  },
}))
